const app = getApp();
import helper from '../common/helper.js';
export default {
	data() {
		return {
			productId: '',
			listInfo: [],
			imgUrl:'',
			// isShow: false,
			vip: ''
		}
	},
	onLoad(e) {
		this.productId = e.id;
		this.imgUrl = helper.imgUrl;
		this.getInfo();
	},
	methods: {
		// 微信小程序除服
		MP_WEIXIN_apply(e) {
			uni.showToast({
				title: '该小程序不支持，请下载APP',
				icon: 'none'
			});
		},
		// APP触发
		APP_PLUS_apply(e) {
			console.log('APP-PLUS')
			let link = e.currentTarget.dataset.link
			let name = e.currentTarget.dataset.name
			if(link == ''){
				uni.showToast({
					title: '该模块正在维修站',
					icon: 'none'
				})
			}
			uni.navigateTo({
				url:`/pages/iframe?name=${name}`
			})
			app.globalData.link = link
		},
		getInfo(){
			uni.request({
				url: `${helper.requestUrl}/holes/detial`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				data:{
					hole_id: this.productId
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 200) {
						this.listInfo = res.data.data
					} else if(res.data.status_code == 202) {
						this.vip = res.data.message
						uni.showToast({
							title: res.data.message,
							icon: 'none',
							duration: 2000
						})
						setTimeout(() => {
							uni.redirectTo({
								url: "meVIP"
							})
						}, 2000)
						// this.isShow = true
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		}
	}
}
